<template>
    <div>
        <h1 class="main-title">注册</h1>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input v-model="userName" size="large" placeholder="请输入用户名"></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input type="password" v-model="password" size="large" placeholder="请输入密码"></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input type="password" v-model="confirmPWD" size="large" placeholder="请输入确认密码"></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Button long type="success" >注册</Button>
            </Col>
        </Row>
    </div>
</template>

<script>
    export default {
        name: "leaving-message",
        data(){
          return {
            userImg:require('../../assets/frontend/common/userPhoto.jpg'),
            userName:'',
            password:'',
            confirmPWD:'',
            content:'',
          };
        },
    }
</script>

<style scoped>

</style>


